export class TitledCoords {
  constructor(title: string, lat: string, lng: string) {
    this.title = title;
    this.lat = lat;
    this.lng = lng;
  }
  public title: string;
  public lat: string;
  public lng: string;
}

export class MapFloor {
  constructor(info: TitledCoords, areas: TitledCoords[], defaultFloor: boolean = false) {
    this.info = info;
    this.areas = areas;
    this.defaultFloor = defaultFloor;
  }
  public info: TitledCoords;
  public areas: TitledCoords[];
  public defaultFloor: boolean;
}

export class MapClinic {
  constructor(info: TitledCoords, id: string, floors: MapFloor[]) {
    this.info = info;
    this.id = id;
    this.floors = floors;
  }
  public info: TitledCoords;
  public id: string;
  public floors: MapFloor[];
}



